		beforeDestroy() {
			window.onscroll = null
		},
		methods: {
			sortDoc(idx) {
				if(this.sortIdx === idx) {
					this.sortIdx = null
				} else {
					this.sortIdx = idx
				}
			},
			searchFor() {
				if(this.inputFlag) {
					return
				}
				this.page2 = 1
				this.pull(false)
			},
			getArea() {
				this.$get('PlatFormAPI/DoctorReferral/QueryAreaIdForProfessionalLevel', {
					DrId: this.$route.query.id,
					ProfessionalLevel: this.$route.query.level
				}).then(o => {
					this.area = o
					this.pull(false)
					this.pullHospital(false)
					this.pullDepartment(0)
					this.pullElse()
				})
			},
			pullHospital(h) {
				this.$post('PlatFormAPI/Doctor/QueryDrHospitalPage', {
					HospitalLevel: -1,
					AreaId: this.area ? this.area.AreaId : 0,
					PageIndex: this.page,
					PageSize: 15
				}).then(o => {
					if(!o.ReturnData || o.ReturnData.length < 15) {
						this.haveMore = false
					} else {
						this.haveMore = true
					}

					if(h) {
						o.ReturnData.forEach(v => {
							this.hospital.push(v)
						})
					} else {
						this.hospital = o.ReturnData
					}
				})
			},
			pullDepartment(id) {
				this.$post('PlatFormAPI/Doctor/QueryDepartmentPage', {
					ParentId: id
				}).then(o => {
					if(id === 0) {
						this.Department = o.ReturnData
					} else {
						this.secDepartment = o.ReturnData
					}
				})
			},
			pullElse() {
				this.$post('PlatFormAPI/Category/QueryCategoryItem', {
					'CategoryIds': [
						9,
						10
					]
				}).then(o => {
					this.Positions = o[0].Items
					this.HospitalLevels = o[1].Items
				})
			},
			pull(h) {
				let AreaId = 0
				if(this.area){
				    AreaId = this.area.AreaId
				}
				this.$post('PlatFormAPI/DoctorReferral/QueryDoctorReferralPage', {
					KeyName: this.search,
					ProfessionalLevel: this.$route.query.level,
					DisGroupId: this.$route.query.DisGroupId,
					HospitalId: this.hos.HospitalId,
					DepartmentId: this.secDep.DepartmentId,
					Position: this.PositionPick ? this.PositionPick.CategoryItemValue : -1,
					IsHasPack: this.IsHasPack,
					HospitalLevel: this.HospitalLevel ? this.HospitalLevel.CategoryItemValue : -1,
					SericeItemCode: this.SericeItemCode,
					PageIndex: this.page2,
					PageSize: 15,
					AreaId,
					SortField: this.sort ? this.sort.SortField : ''
				}).then(o => {
					this.show3 = false
					this.loading = false
					if(!o.ReturnData || o.ReturnData.length < 15) {
						this.haveMore2 = false
					} else {
						this.haveMore2 = true
					}

					if(h) {
						o.ReturnData.forEach(v => {
							this.doctors.push(v)
						})
					} else {
						this.doctors = o.ReturnData
					}
					this.pageShow = true
				})
			},
			hosPick(i) {
				this.hos = i
				this.sortList[0] = i.HospitalName
				this.sortIdx = null
				this.page2 = 1
				this.pull(false)
			},
			depPick(i) {
				this.dep = i
				if(i.DepartmentId === -1) {
					this.secDepartment = []
					this.secDep = i
					this.sortList[1] = '全部科室'
					this.sortIdx = null
					this.page2 = 1
					this.pull(false)
				} else {
					this.pullDepartment(i.DepartmentId)
				}
			},
			secDepPick(i) {
				this.secDep = i
				this.sortList[1] = i.DepartmentName
				this.sortIdx = null
				this.page2 = 1
				this.pull(false)
			},
			sortPick(i) {
				this.sort = i
				this.sortList[2] = i.name
				this.sortIdx = null
				this.page2 = 1
				this.pull(false)
			},
			reset() {
				this.IsHasPack = -1,
					this.SericeItemCode = null,
					this.HospitalLevel = null,
					this.PositionPick = null
			},
			confirm() {
				this.sortIdx = null
				this.page2 = 1
				this.pull(false)
			},
			showConfirmBox(i) {
				this.selectDrMsg = i
				this.selectDrMsg.fromList = true
				this.selectDrMsg.ProfessionalLevel = this.$route.query.level
				this.selectDrMsg.primaryDr = this.$route.query.id
				this.showConfirm = true
			},
			onConfirm() {
				/*let drMsg = {
					"DrId": 77,
					"DrName": "张卫健",
					"Mobile": "13125556664",
					"DrType": 1,
					"DrSex": 1,
					"DrStatus": 0,
					"HospitalId": 1,
					"HospitalName": "中日医院",
					"DepartmentId": 2,
					"DepartmentName": "外科",
					"Position": 2,
					"PositionName": "副主任医师",
					"DoctorGroup": null,
					"PicturePath": null,
					"TreatMent": null,
					"DoctorSkill": "肝癌",
					"Description": null,
					"IsOnline": 1,
					"CreateTime": "2018-09-20 10:11:05",
					"PraiseRate": 0,
					"ConsultCount": 0,
					"ResponseRate": 0,
					"ConsultVolume": 0,
					"OnPrescription": 0,
					"VisitCount": 0,
					"Telenursing": 0,
					"VolumeVideo": 0,
					"FetalHeart": 0,
					"SericeItemCodes": "1101|图文问诊|1.00|0.00,1102|视频问诊|11.00|1.00,1103|名医续方|10.00|10.00",
					"DrServiceItems": [{
						"SericeItemCode": "1101",
						"SericeItemName": "图文问诊",
						"OriginalPrice": "1.00",
						"Price": "0.00"
					}, {
						"SericeItemCode": "1102",
						"SericeItemName": "视频问诊",
						"OriginalPrice": "11.00",
						"Price": "1.00"
					}, {
						"SericeItemCode": "1103",
						"SericeItemName": "名医续方",
						"OriginalPrice": "10.00",
						"Price": "10.00"
					}],
					"IsPatientMain": true,
					"IsAttantDr": true,
					"IsHasPack": 0,
					"PatientAttentDrNum": 3,
					"MedicalRecord": 0,
					"MedicalPrice": "0.00",
					"InterVolume": 0,
					"IsOpenPresc": 0,
					"OpratePrescDrId": 0,
					"OrgId": 1,
					"OrgName": "新起点互联网医院",
					"IsClinic": 0,
					"IsReferral": 1,
					"ProfessionalLevel": 2,
					"AreaName": "朝阳区"
				
				}
				let drInfo = this.$store.state.ProDrInfos
				let level = Number(drMsg.ProfessionalLevel)*/

				
				let drMsg = this.selectDrMsg
				let drInfo = this.$store.state.ProDrInfos
				let level = Number(drMsg.ProfessionalLevel)
				if(drMsg.DrId == this.loginDrMsg.DoctorId){
					this.$toast("温馨提示：转诊医生不能选择当前登录用户")
					return false;
				}
				drInfo[level - 1] = drMsg
				let n = ""
				switch(level) {
					case 1:
						n = '基层医生'
						break;
					case 2:
						n = '乡级医生'
						break;
					case 3:
						n = '区级医生'
						break;
					case 4:
						n = '市级医生'
						break;
					case 5:
						n = '省级医生'
						break;
					default:
						break;
				}
				
				drInfo[level - 1].ProfessionalName = n
				this.drInfos = drInfo
				this.flag = 6
				this.primaryDr = drMsg.primaryDr
					//ProDrInfos
				//保存医生默认配置
				let infoMsg = {}
				this.drInfos.forEach((n, idx) => {
					if(n.ProfessionalLevel) {
						switch(n.ProfessionalLevel) {
							case 1:
								//'基层管理医生'
								infoMsg.BasicDrId = n.DrId || null
								infoMsg.BasicDrName = n.DrName || null
								break;
							case 2:
								//'乡村级管理医生'
								infoMsg.CountryDrId = n.DrId || null
								infoMsg.CountryDrName = n.DrName || null
								break;
							case 3:
								//'区级管理医生'
								infoMsg.AreaDrId = n.DrId || null
								infoMsg.AreaDrName = n.DrName || null
								break;
							case 4:
								//'市级管理医生'
								infoMsg.CityDrId = n.DrId || null
								infoMsg.CityDrName = n.DrName || null
								break;
							case 5:
								//'省级管理医生'
								infoMsg.ProvinceDrId = n.DrId || null
								infoMsg.ProvinceDrName = n.DrName || null
								break;
							default:
								break;
						}
					}
					
				})
				infoMsg.DisGroupId = this.$route.query.DisGroupId
				infoMsg.DisGroupName = this.$route.query.DisGroupName
				if(this.$route.query.fromWay){
					infoMsg.FromDrId = this.loginDrMsg.DoctorId
					infoMsg.FromDrName = this.loginDrMsg.DoctorName
					infoMsg.PatientId = this.$route.query.PatientId
					infoMsg.PatientName = this.$route.query.PatientName
					this.$post('PlatFormAPI/DoctorReferral/SavePatientReferralSet', infoMsg).then(o => {
						//保存患者的转诊团队
						this.$store.dispatch('pushProDrInfos', this.drInfos)
						this.$store.dispatch('pushDisGroupMsg', {DisGroupId:this.$route.query.DisGroupId,primaryDr:this.primaryDr})
						this.showConfirm = false
						this.$router.back()
					})
				}else{
					this.$post('PlatFormAPI/DoctorReferral/SaveDoctorReferralSet', infoMsg).then(o => {
						//保存医生的默认转诊团队
						this.$store.dispatch('pushProDrInfos', this.drInfos)
						this.$store.dispatch('pushDisGroupMsg', {DisGroupId:this.$route.query.DisGroupId,primaryDr:this.primaryDr})
						this.showConfirm = false
						this.$router.back()
					})
				}
			},

			getMore() {
				//加载更多
				const el = this.$refs.el
				let timer = null
				el.onscroll = () => {
					clearTimeout(timer)
					timer = setTimeout(() => {
						const top = el.scrollTop ||
							el.pageYOffset ||
							el.scrollTop,
							h = el.scrollHeight,
							wh = el.offsetHeight
						if(h - top - wh < 100 && this.haveMore) {
							this.page += 1
							this.pullHospital(true);
						}
					}, 100)
				}
				const el2 = this.$refs.el3
				let timer2 = null
				el2.onscroll = () => {
					clearTimeout(timer2)
					timer2 = setTimeout(() => {
						const top = el2.scrollTop ||
							el2.pageYOffset ||
							el2.scrollTop,
							h = el2.scrollHeight,
							wh = window.innerHeight
						this.scrollTopDistance = top;
						if(h - top - wh < -90 && this.haveMore2) {
							this.page2 += 1
							this.loading = true
							this.pull(true);
						}
					}, 100)
				}
			},
			reload() {
				const el2 = this.$refs.el2
				const el3 = this.$refs.el3
				let startX = null
				let startY = null
				let endX = null
				let endY = null
				el2.addEventListener('touchstart', e => {
					this.show3 = false
					startX = e.changedTouches[0].pageX
					startY = e.changedTouches[0].pageY
				})
				el2.addEventListener('touchmove', e => {
					endX = e.changedTouches[0].pageX
					endY = e.changedTouches[0].pageY
					if(this.sortIdx !== null) {
						return
					}
					if(Math.abs(startX - endX) < Math.abs(startY - endY) && (startY - endY) < 0) {
						const top = el3.scrollTop ||
							el3.pageYOffset
						if(top < 10 || !top) {
							this.show3 = true
						}
					}
				})
				el2.addEventListener('touchend', () => {
					if(this.show3) {
						this.page2 = 1
						this.haveMore2 = true
						this.pull(false)
					}
				})
			}
		}
	}



